@import "~antd/dist/antd.css";
/*@import "../src/style/rewaer-antd.css";*/

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(16px + 1vmin);
  letter-spacing: 1.5px;
}

.App a:link, a:visited {
  color: rgba(109, 145, 126, .75);
}

.App a:hover {
  color: rgba(109, 145, 126, 1);
}

.link {
  color: rgba(109, 145, 126, .75);
  cursor: pointer;
} 

.link:hover {
  color: rgba(109, 145, 126, 1);
}

